<template>
  <div class="myIssue">
    <!-- 头部 -->
    <PublicHeader>{{state==1?'我的发布':'我的收藏'}}</PublicHeader>
    <!-- 滚动列表 -->
    <div class="wrapper" ref="wrapper" v-if="login">
      <div class="content">
        <div class="infos" v-for="(item, index) in list" :key="index"
        @click="jump(item)">
          <!-- 头像 -->
          <div class="avator">
            <div class="left">
              <img :src='item.avator' alt="">
              <div class="name">
                <h3>{{item.userName}}</h3>
                <p>{{item.time}}</p>
              </div>
            </div>
            <i class="iconfont">&#xe60b;</i>
          </div>
          <!-- 文本 -->
          <div :class="showAll?'text':'text es3'">
            <p>{{item.content.text}}</p>
          </div>
          <!-- 图片 -->
          <div class="imgs">
            <img :src="item" alt="" v-for="(item, index) in item.content.imgs" :key="index">
            <span v-if="item.content.imgs.length%3==2"></span>
          </div>
        </div>
      </div>
    </div>
    <NoLogin v-else></NoLogin>
  </div>
</template>
<script>
import Bs from 'better-scroll'
import { getMyIssue } from 'api/api'
import { mapMutations } from 'vuex'
import { handleDataIssue } from 'utils/handleData'
import NoLogin from 'components/NoLogin'
import PublicHeader from 'components/PublicHeader'
export default {
  components: { PublicHeader, NoLogin },
  data () {
    return {
      login: false,
      list: [],
      state: 1,
      showAll: false,
      avator: ''
    }
  },
  methods: {
    ...mapMutations(['addInfoData']),
    initBs () {
      const wrapper = this.$refs.wrapper
      /* eslint-disable no-new */
      new Bs(wrapper, { click: true })
    },
    getInitData () {
      let userInfo = localStorage.getItem('userInfo')
      userInfo = JSON.parse(userInfo)
      const userId = userInfo.userId
      this.avator = userInfo.avator
      getMyIssue(userId).then((res) => {
        const data = handleDataIssue(res.data, this.baseUrl)
        this.list = data
        this.state = this.$route.params.state
        this.$nextTick(() => {
          this.initBs()
        })
      })
    },
    jump (item) {
      const userName = this.userName
      const title = item.title
      const content = item.content
      const avator = this.baseUrl + this.avator
      const look = item.look
      const collect = item.collect
      const share = item.share
      const time = item.time
      let data = item
      if (this.state === '1') {
        data = { userName, title, content, avator, look, collect, share, time }
      }
      this.addInfoData(data)
    }
  },
  mounted () {
    let userInfo = localStorage.getItem('userInfo')
    if (!userInfo) { this.login = false; return false }
    userInfo = JSON.parse(userInfo)
    if (!userInfo.state) { this.login = false; return false }
    if (userInfo.state) {
      this.login = true
    }
    if (!this.list.legth === 0) { return false }
    this.state = this.$route.query.state
    this.getInitData()
  }
}
</script>
<style lang="less" scoped>
.myIssue{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  background: #fff;
  .title{
    width: 6.9rem;
    margin: auto;
    line-height: .88rem;
    text-align: center;
    position: relative;
    color: #222;
    h1{
      font-size: .34rem;
    }
    .iconfont{
      font-size: .34rem;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  .wrapper{
    border-top: 1px solid #dededf;
    position: absolute;
    top: .88rem;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    .content{
      padding-bottom: .2rem;
      .infos{
        padding: 0 .3rem;
        border-top: 2px solid #f8f8f8;
        .avator{
          margin: .2rem 0;
          display: flex;
          justify-content: space-between;
          align-items: center;
          .left{
            flex: 1;
            display: flex;
            img{
              width: .8rem;
              height: 0.8rem;
              border-radius: 50%;
              margin-right: .3rem;
            }
            .name{
              font-size: .28rem;
              line-height: 0.42rem;
              color: #333;
              p{
                font-size: .2rem;
                line-height: 0.38rem;
                color: #999999;
              }
            }
          }
          .iconfont{
            font-size: .16rem;
            color: #9d9d9d;
          }
        }
        .text{
          font-size: .28rem;
          line-height: 0.44rem;
          color: #323232;
          text-align: justify;
        }
        .imgs{
          margin: .12rem 0 .22rem;
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          img{
            width: 32%;
            height: 2rem;
            border-radius: .2rem;
            margin-bottom: .1rem;
          }
          span{
            display: block;
            width: 32%;
            height: 2rem;
            border-radius: .2rem;
          }
        }
      }
    }
  }
}
</style>
